	module.exports = {"data":{"markdownRemark":{"html":"<p>By default, the Free Code Camp site automatically loads and runs your last recorded solution. If you have accidentally created an infinite loop or other irrecoverable error or simply don’t trust the code, you can disable code auto-run by putting the following in your URL: <code>run=disabled</code></p>\n<h2>Example:</h2>\n<p>URL: <code>http://www.freecodecamp.com/challenges/Algorithm-meet-Algorithm#?solution=function%20meetBonfire(argument)</code></p>\n<p>No-Run URL: <code>http://www.freecodecamp.com/challenges/Algorithm-meet-Algorithm#?run=disabled&#x26;solution=function%20meetBonfire(argument)</code></p>","fields":{"slug":"/miscellaneous/disable-code-auto-run-on-free-code-camp/"},"frontmatter":{"title":"Disable Code Auto Run on Free Code Camp"}}},"pathContext":{"slug":"/miscellaneous/disable-code-auto-run-on-free-code-camp/"}}

